import React, { useContext, useEffect, useState, useMemo } from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'

import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import RadioButton from 'components/RadioButton'
import { selectPayToken, setTokenPay } from 'pages/Marketplace/store/reducer'
import { MarketplaceContext } from 'pages/Marketplace/context/MarketplaceContext'
import { selectPayTokenConfig } from 'state/reducers/app'

import TabButton, { TAB_BUTTON_TYPE } from '../LeftFilter/Tabs/TabButton.js'

const Wrapper = styled(Flex)`
  min-width: 200px;
  flex: 1;
  justify-content: flex-end;
`

const Container = styled(Flex)`
  position: relative;
  align-items: center;
`
const ResultsText = styled(Text)`
  // min-width: 126px;
  position: relative;
  font-style: normal;
  font-weight: 500;
  font-size: 1.3rem;
  margin-right: 8px;
  color: #e0e0e0;
`

const PayToken = ({ setQuery }) => {
  const { state, marketplaceDispatch } = useContext(MarketplaceContext)
  const curTokenPay = useSelector(() => selectPayToken(state))
  const payTokenConfig = useSelector(selectPayTokenConfig)
  const [radioValue, setRadio] = useState(false)
  const onHandleUpdateTokenPay = (payToken) => {
    setQuery({
      payToken: payToken.address,
      pageNumber: 1,
    })
    // marketplaceDispatch(setTokenPay(tokenPay))
  }

  return (
    <Wrapper>
      {payTokenConfig && payTokenConfig.payTokens ? (
        payTokenConfig.payTokens.map((el) => {
          return (
            <Container key={el.symbol}>
              <RadioButton
                name="radio"
                value={curTokenPay ? curTokenPay.toLowerCase() === el.address.toLowerCase() : false}
                checked={curTokenPay ? curTokenPay.toLowerCase() === el.address.toLowerCase() : false}
                onChange={() => {
                  onHandleUpdateTokenPay(el)
                }}
              />
              <ResultsText ml="8px">{el.symbol}</ResultsText>
            </Container>
          )
        })
      ) : (
        <></>
      )}
    </Wrapper>
  )
}

export default PayToken
